.card{
    width: 150px;
    height: 200px;
    background: $color3;
    border:3px solid #fff;
    box-shadow: 0 0 0 1px darken($color1, 5%);
    cursor: pointer;
    padding: 3px 5px;
    position: relative;
    &.fig1,&.fig3{
        color: $color6;
    }
    &.cut{
        border-bottom: 0;
        height: 30px;
        .figCenter{
        display: none;
        }
    }
    &.hidden{
        cursor: initial ;
        background: $color2 center;
        border:3px  solid lighten($color2, 17%);
        background-image: url('../Assets/pattern-card.png');
        border-bottom: 0;
        outline: none;
    }
    .cardBox &{
        &.cut{
            height: 200px;
            
        }
    }
    &.invisible{
        height: 0;
        opacity: 0;
        border: 0;
        &:not(.unique){
            pointer-events: none;
        }
    }
    .finalcols &{
    width: 75px;
    height: 100px;
    }
    &.last{
        background-color: darken($color1, 5%);
        background-image: none;
        border: none !important;
        font-size: 0;
    }
    .figCenter{
        font-size: 70px;
        position: absolute;
        top:50%;
        left: 50%;
        transform: translate(-50%,-50%);
        pointer-events: none;
    }
    .col &{
        .figCenter{
            transform: translate(-50%,-55%);
        }
    }
}